import axios, { AxiosError } from 'axios'
import { useSnackbar } from 'notistack'
import { useAuthHeader } from 'react-auth-kit'
import { useQuery } from 'react-query'

import { COMMON_MESSAGE } from 'src/data/messages'

import { type ClubPreviewProps } from 'src/components/ClubPreview'

import { type SearchRecruitsResponseBody } from 'src/responses/search'

interface SearchAPIResponse {
  clubs: ClubPreviewProps[]
  numPages: number
}

const getDefaultRecruitsAPICall = (count: number) => async () => {
  const response = await axios.get<SearchRecruitsResponseBody>(`${process.env.REACT_APP_APIHOST}/search/recruits`, {
    params: { count: count }
  })
  return response.data
}

export const useGetDefaultRecruits = (count: number) => {
  const { enqueueSnackbar } = useSnackbar()

  return useQuery<SearchRecruitsResponseBody>('defaultRecruits', getDefaultRecruitsAPICall(count), {
    onError: error => {
      if (!(error instanceof AxiosError)) {
        enqueueSnackbar(COMMON_MESSAGE.UNKNOWN_ERROR, { variant: 'error' })
      }
    }
  })
}

const getClubsByParam = (apiParams: URLSearchParams, token: string) => async () => {
  const response = await axios.get<SearchAPIResponse>(`${process.env.REACT_APP_APIHOST}/search/clubs`, {
    params: apiParams,
    headers: { Authorization: token }
  })
  return response.data
}

export const useGetClubsByParam = (searchParams: URLSearchParams) => {
  const { enqueueSnackbar } = useSnackbar()
  const token = useAuthHeader()()
  const response = useQuery<SearchAPIResponse>('clubsByParam', getClubsByParam(searchParams, token), {
    onError: error => {
      if (!(error instanceof AxiosError)) {
        enqueueSnackbar('서버에서 오류가 발생한 것 같아요. 다시 시도해주세요', {
          variant: 'error'
        })
      }
    }
  })
  return response
}
